<template>
  <v-container>
    <v-row class="text-center mt-5">
      <v-col
        cols="3"
      >
    <h2 class="text-left">Welcome</h2>
    <p class="text-justify">
     This website allows you to track all-cause mortality in various age groups on a weekly
     basis for a variety of European countries and regions.
      </p>
      </v-col>
      <v-col
        cols="1"
      ></v-col>
      <v-col
        cols="2"
      >

          <v-select
            v-model="countrySelection"
            :items="countryOptions"
            item-value="name"
            item-text="name"
            label="Region"
            outlined
          ></v-select>
      </v-col>

      <v-col
        cols="3"
      >
        <v-container fluid>
          <v-select
            v-model="selectedAgeGroups"
            :items="ageGroups"
            label="Age groups"
            item-text="name"
            item-value="value"
            multiple
          ></v-select>
          <v-btn
            @click="getChart1Data(countrySelection, selectedAgeGroups, yearSelection);
            getChart2Data(countrySelection, selectedAgeGroups, yearSelection,
                                                        calendarWeekSelection);;
            getChart3Data(countrySelection, selectedAgeGroups, yearSelection,
                                                        calendarWeekSelection)"
            depressed
            elevation="2"
            class="font-weight-bold"
          >Draw plot</v-btn>
        </v-container>
      </v-col>

      <v-col
        cols="2"
      >
          <v-select
            v-model="yearSelection"
            :items="yearOptions"
            label="Starting year"
            outlined
          ></v-select>
      </v-col>

      <v-col
        cols="1"
      >
      </v-col>
    </v-row>
    <v-row class="text-left">
      <v-col
        cols="3">
        <h2>Methodology</h2>
        <p>Weekly mortality data is pulled from EUROSTAT and normalized with the population
          of the selected age group on January 1st of the year in question. To calculate the
          expected deaths the weekly data is grouped by calendar week and then averaged across
          all years. Outliers deviating more then one standard deviation from the mean are removed
          before averaging.
        </p>
      </v-col>
      <v-col
        cols="1">
      </v-col>
      <v-col class="mb-4" cols="7">
        <h1 v-if="dataQuerySuccessful == null">
        Please wait while the data is being fetched
        </h1>
        <v-overlay v-if="dataQuerySuccessful == null"></v-overlay>
        <v-progress-linear
          indeterminate
          color="primary"
          v-if="dataQuerySuccessful == null"
        ></v-progress-linear>
        <h2 v-if="Chart1Data_success" class="font-weight-bold mb-3 text-center">
          Deaths per million chart for region {{countrySelection}}
        </h2>
        <chart-1 v-if="Chart1Data_success" :chartData="datacollection" class="mt-3"></chart-1>
      </v-col>
      <v-col
        cols="1">
      </v-col>
    </v-row>
    <v-row class="text-left">
      <v-col
        cols="3">

        <h2>Note on expected deaths</h2>
        <p>The term expected deaths usually implies that for a given time <b>t</b>,
        to calculate the expectation for <b>t</b>, we only consider data in
        the past from the point of view of <b>t</b>. For simplicity's sake both
        data in the past and future are considered for each data point, resulting
        in an outlier-adjusted average, rather than a proper expectation value.</p>
      </v-col>
      <v-col
        cols="1">
      </v-col>
      <v-col
        cols="7">
        <h2 v-if="Chart2Data_success" class="text-center font-weight-bold mb-3">
          Aggregated yearly deaths per million for region {{countrySelection}} until
          calendar week {{this.latest_week}}
        </h2>
        <chart-2 v-if="Chart2Data_success" :chartData="datacollectionChart2"></chart-2>
      </v-col>
      <v-col
        cols="1">
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';
import Chart1 from './Chart1.vue';
import Chart2 from './Chart2.vue';
// import Chart3 from './Chart3.vue';

export default {
  name: 'Homepage',

  data: () => ({
    Chart1Data_success: null,
    Chart2Data_success: null,
    Chart3Data_success: null,
    calendarWeekSelection: null,
    calendarWeekOptions: Array.from({ length: 52 }, (x, i) => i + 1),
    countrySelection: '',
    countryOptions: [{ name: 'Sweden', code: 'SE' }, { name: 'Switzerland', code: 'CH' }, { name: 'Portugal', code: 'PT' },
      { name: 'Spain', code: 'ES' }, { name: 'France', code: 'FR' }, { name: 'Région lémanique', code: 'CH01' },
      { name: 'Espace Mittelland', code: 'CH02' }, { name: 'Nordwestschweiz', code: 'CH03' }, { name: 'Zürich', code: 'CH04' },
      { name: 'Ostschweiz', code: 'CH05' }, { name: 'Zentralschweiz', code: 'CH06' }, { name: 'Ticino', code: 'CH07' },
      { name: 'Comunidad de Madrid', code: 'ES3' }, { name: 'Belgium', code: 'BE' }, { name: 'Denmark', code: 'DK' },
      { name: 'Poland', code: 'PL' }, { name: 'Netherlands', code: 'NL' }, { name: 'Austria', code: 'AT' },
      { name: 'Italy', code: 'IT' }, { name: 'Finland', code: 'FI' }, { name: 'United Kingdom', code: 'UK' }],
    selectedAgeGroups: '',
    yearOptions: [2018, 2017, 2016, 2015, 2014, 2013, 2012, 2011, 2010],
    yearSelection: '',
    datacollection: {},
    dataQuerySuccessful: null,
    datacollectionChart2: {},
    labels: [],
    latest_week: null,
    deaths_per_million: [],
    ageGroups: [{ name: 'Years 40-44', value: 'Y40-44' }, { name: 'Years 45-49', value: 'Y45-49' },
      { name: 'Years 50-54', value: 'Y50-54' }, { name: 'Years 55-59', value: 'Y55-59' },
      { name: 'Years 60-64', value: 'Y60-64' }, { name: 'Years 65-69', value: 'Y65-69' },
      { name: 'Years 70-74', value: 'Y70-74' }, { name: 'Years 75-79', value: 'Y75-79' },
      { name: 'Years 80-84', value: 'Y80-84' }, { name: 'Years 85-89', value: 'Y85-89' },
      { name: 'Years 90 and older', value: 'Y_GE90' }],
    sideBarItems: [
      { title: 'Dashboard', icon: 'mdi-view-dashboard' },
      { title: 'Photos', icon: 'mdi-image' },
      { title: 'About', icon: 'mdi-help-box' },
    ],

  }),

  components: {
    'chart-1': Chart1,
    'chart-2': Chart2,
    // 'chart-3': Chart3,
  },

  mounted() {
    // const dataQueryPath = 'https://mortalitymonitorbackend.azurewebsites.net/data';
    const dataQueryPath = 'https://pombolutador.pythonanywhere.com/data';
    // const dataQueryPath = 'http://localhost:5000/data';
    axios.post(dataQueryPath, {
      data: {
        geo_codes: ['SE', 'CH', 'PT', 'BE', 'CH04', 'FR', 'ES', 'CH01',
          'CH02', 'CH03', 'CH04', 'CH05', 'CH06', 'CH07', 'ES3', 'DK',
          'PL', 'NL', 'AT', 'IT', 'FI', 'UK'],
        sincetimeperiod: '2010',
        precision: '0',
        mortality_ages: ['Y60-64', 'Y65-69', 'Y70-74', 'Y75-79', 'Y80-84', 'Y85-89', 'Y_GE90'], // ['Y40-44', 'Y45-49', 'Y50-54', 'Y55-59', 'Y60-64',
        sex: 'T',
      },
    })
      .then((res) => {
        this.dataQueryMessage = res.data;
        this.dataQuerySuccessful = true;
      })
      .catch((error) => {
        console.error(error);
      });
  },
  // };
  methods: {
    getChart1Data(geoChoice, ageGroupChoices, yearChoice) {
      // const endpointUrl = 'https://mortalitymonitorbackend.azurewebsites.net/Chart1_data';
      const endpointUrl = 'https://pombolutador.pythonanywhere.com/Chart1_data';
      // const endpointUrl = 'http://localhost:5000/Chart1_data';
      axios.post(endpointUrl, {
        data: {
          geo: geoChoice,
          ages: ageGroupChoices.sort(),
          time: yearChoice,
        },
      })
        .then((res) => {
          this.labels = res.data.weekly_data.label;
          this.years = res.data.weekly_data.year;
          this.deaths_per_million = res.data.weekly_data.deaths_per_million;
          this.excess_deaths = res.data.weekly_data.excess_deaths;
          this.expected_deaths = res.data.weekly_data.expected_deaths;
          this.above_expectation_deaths = res.data.weekly_data.above_expectation_deaths;
          this.below_expectation_deaths = res.data.weekly_data.below_expectation_deaths;
          this.datacollection = {
            labels: this.labels,
            datasets: [
              {
                pointBackgroundColor: '#000000',
                pointBorderColor: '#000000',
                borderWidth: 1,
                pointRadius: 1,
                type: 'line',
                backgroundColor: '#000000',
                borderColor: '#000000',
                label: 'Expected deaths per millions',
                data: this.expected_deaths,
                fill: false,
                lineTension: 0,
              },
              {
                backgroundColor: '#F44336',
                label: 'Excess deaths per millions',
                data: this.above_expectation_deaths,
              },
              {
                backgroundColor: '#4CAF50',
                label: 'Below expectation deaths per millions',
                data: this.below_expectation_deaths,
              },
              {
                backgroundColor: '#1A237E',
                label: 'Deaths per millions',
                data: this.deaths_per_million,
              },
            ],
          };
        });
      this.Chart1Data_success = true;
    },
    getChart2Data(geoChoice, ageGroupChoices, yearChoice) {
      // const endpointUrl = 'https://mortalitymonitorbackend.azurewebsites.net/Chart2_data';
      // const endpointUrl = 'http://localhost:5000/Chart2_data';
      const endpointUrl = 'https://pombolutador.pythonanywhere.com/Chart2_data';
      axios.post(endpointUrl, {
        data: {
          geo: geoChoice,
          ages: ageGroupChoices.sort(),
          time: yearChoice,
          // calendarWeek: calendarWeekSelection,
        },
      })
        .then((res) => {
          this.labels = res.data.yearly_deaths.years;
          this.actual_deaths_per_million = res.data.yearly_deaths.actual_deaths_per_million;
          this.expected_deaths_per_million = res.data.yearly_deaths.expected_deaths_per_million;
          this.excess_deaths_per_million = res.data.yearly_deaths.excess_deaths_per_million;
          this.latest_week = res.data.yearly_deaths.latest_week;
          this.datacollectionChart2 = {
            labels: this.labels,
            datasets: [
              {
                backgroundColor: '#1A237E',
                label: 'Yearly deaths per million',
                data: this.actual_deaths_per_million,
              },
            ],
          };
        });
      this.Chart2Data_success = true;
    },
    getChart3Data(geoChoice, ageGroupChoices, yearChoice, calendarWeekSelection) {
      // const endpointUrl = 'https://mortalitymonitorbackend.azurewebsites.net/Chart2_data';
      const endpointUrl = 'https://localhost:5000/Chart3_data';
      axios.post(endpointUrl, {
        data: {
          geo: geoChoice,
          ages: ageGroupChoices.sort(),
          year: yearChoice,
          calendarWeek: calendarWeekSelection,
        },
      })
        .then((res) => {
          this.labels = res.data.labels;
          this.values = res.data.values;
          this.datacollectionChart3 = {
            labels: this.labels,
            datasets: [
              {
                backgroundColor: '#1A237E',
                label: 'Aggregated population for selected age groups',
                data: this.values,
              },
            ],
          };
        });
      this.Chart3Data_success = true;
    },
  },
};
</script>
